.ubiquitiBlackBackground {
    background: #202830; /* For browsers that do not support gradients */
    background: -webkit-gradient(linear, left top, left bottom, from(#202830), to(#000000));
    background: -webkit-linear-gradient(#202830, #000000);
    background: -o-linear-gradient(#202830, #000000);
    background: linear-gradient(#202830, #000000);
}


